.home-wrap {
  background-color: #fff;
  height: calc(62vh);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 20px; }

ul,
li {
  list-style: none; }

.clearfix:after {
  content: " ";
  clear: both;
  display: block;
  visibility: hidden; }

.container .nav-left {
  min-width: 180px;
  color: #ffffff; }

.container .main {
  height: calc(100vh);
  background-color: #f1f3f5;
  overflow: auto; }

.container .content {
  position: relative;
  padding: 20px; }

.content-wrap {
  background: #ffffff;
  border: 1px solid #e8e8e8;
  margin-top: -3px; }
  .content-wrap .ant-table-wrapper {
    margin-left: -1px;
    margin-right: -2px; }

.operate-wrap button {
  margin-right: 10px; }

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0; }

.link-button:hover,
.link-button:focus {
  text-decoration: none; }

